@import url(https://fonts.googleapis.com/css?family=Lato);
*{box-sizing:border-box;font-family:Lato, Arial, sans-serif}body{background-color:#282c34;color:#dcdcdc}.App{margin:0 auto;max-width:1300px}.App h1{text-align:center}.App fieldset{display:flex;justify-content:center;flex-direction:column;width:65%;margin:0 auto;padding:20px;border:none;border-radius:10px;background:linear-gradient(to bottom right, #3c3f4a, #2e313c)}.App fieldset label{display:block;margin-bottom:5px}.App fieldset input{margin-bottom:10px;padding:10px;border:1px solid #5c5f6f;border-radius:50px;background:linear-gradient(to bottom right, #3c3f4a, #2e313c);color:#dcdcdc;font-size:16px}.App fieldset input:focus{border-color:#a3a6b9;outline:none}.App fieldset input:last-child{margin-left:10px}.App fieldset button{margin-top:10px;padding:10px 20px;border:none;border-radius:50px;background:linear-gradient(to bottom right, #5c5f6f, #30333e);color:#dcdcdc;font-size:16px;cursor:pointer}.App fieldset button:hover{background:linear-gradient(to bottom right, #a3a6b9, #454a5d)}.App fieldset button:focus{outline:none}.App fieldset button:disabled{opacity:0.5;cursor:not-allowed}

.Carousel{margin:0 auto}.Carousel__list{display:flex;list-style:none;-webkit-padding-start:0;padding-inline-start:0}.Carousel__button{display:flex;justify-content:space-between}.Carousel__button--item{width:150px;padding:10px;font-size:36px;font-weight:bold;border:none;border-radius:50px;background:linear-gradient(to right, #f0932b, #e67e22);color:#2d3436}.Carousel__button--item:hover{cursor:pointer;background:linear-gradient(to right, #e67e22, #f0932b)}.Carousel__button--item:disabled{background:linear-gradient(to right, #b3b3b3, #b3b3b3);color:white;cursor:not-allowed}

